<template>
  <div class="common-detail">
    <div class="content">
      <Form :label-width="120">
          <FormItem label="发起机构：">
            <label>
             <!-- {{watchTableCell.val1}} -->
                               <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val1"
                  >
                    <p slot="reference" class="textMaxWidth2 disBlock textOverflow" >{{watchTableCell.val1}}</p>
                  </el-popover>
            </label>
          </FormItem>
          <FormItem label="参与机构：">
            <label>
              <!-- {{watchTableCell.val2}} -->
                                             <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val2"
                  >
                    <p slot="reference" class="textOverflow disBlock textMaxWidth2">{{watchTableCell.val2}}</p>
                  </el-popover>
            </label>
          </FormItem>
          <FormItem label="宣传内容：">
            <!-- <label>{{watchTableCell.val3}}</label> -->
                                                         <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val3"
                  >
                    <p slot="reference" class="textOverflow disBlock textMaxWidth2">{{watchTableCell.val3}}</p>
                  </el-popover>
          </FormItem>
          <FormItem label="宣传方式：">
            <label>{{watchTableCell.val4}}</label>
          </FormItem>
          <FormItem label="宣传时间：">
            <label>{{watchTableCell.val5[0]}}</label>
          </FormItem>
          <FormItem label="受众人数：">
            <label>{{watchTableCell.val6}}</label>
          </FormItem>
          <FormItem label="发放宣传资料份数：">
            <label>{{watchTableCell.val7}}</label>
          </FormItem>
          <FormItem label="宣传效果：">
            <!-- <label>{{watchTableCell.val8}}</label> -->
                                                         <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val8"
                  >
                    <p slot="reference" class="textOverflow disBlock textMaxWidth2">{{watchTableCell.val8}}</p>
                  </el-popover>
          </FormItem>
          <FormItem label="是否公开">
            <label>{{watchTableCell.val9?'是':'否'}}</label>
          </FormItem>
          <FormItem label="编辑内容：">
                  <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val10"
                  >
                    <p slot="reference" class="textOverflow disBlock textMaxWidth textTdTop">{{watchTableCell.val10}}</p>
                  </el-popover>
          </FormItem>
          <FormItem label="附件：">
            <showUsualFiles
              :file-obj="dataObj4"
              :key="fileId4"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加附件：">
            <showUsualFiles
              :file-obj="dataObj5"
              :key="fileId5"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加内容：">
            <!-- <label><p>{{watchTableCell.val11}}</p></label> -->
                              <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val11"
                  >
                    <p slot="reference" class="textOverflow disBlock textMaxWidth2">{{watchTableCell.val11}}</p>
                  </el-popover>
          </FormItem>
        </Form>
    </div>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
export default {
  props: ["dataObj"],
  components: {
    showUsualFiles
  },
  data() {
    return {
      watchTableCell: {
        val1: "",
        val2: "",
        val3: "",
        val4: "",
        val5: "",
        val6: "",
        val7: "",
        val8: "",
        val9: "",
        val10: "",
        val11: ""
      },
      fileId4: "",
      fileId5: "",
      dataObj4: {},
      dataObj5: {}
    };
  },
  created() {
    let data = this.dataObj.data;
    if (data) {
      this.watchTableCellFun(data, data.busId, data.id);
    }
  },
  methods: {
    watchTableCellFun(val1, val2, val3) {
      this.axios
        .post(`/process/infPublicity/getEntityRow/${val3}`+'?infViewNum=1')
        .then(response => {
          
          if (response.code == "200") {
               this.watchTableCell.val1=response.data.launchOrg,
               this.watchTableCell.val2=response.data.joinOrg,
              this.watchTableCell.val3=response.data.publicityContent,
              this.watchTableCell.val4=response.data.publicityType,
              this.watchTableCell.val5=[];
              this.watchTableCell.val5.push(response.data.publicityStartTime);
              this.watchTableCell.val5.push(response.data.publicityEndTime);
              this.watchTableCell.val6=response.data.audienceNum,
              this.watchTableCell.val7=response.data.distributionNum,
              this.watchTableCell.val8=response.data.publicityResult,
              this.watchTableCell.val9=JSON.parse(response.data.ifShow),
              this.watchTableCell.val10=response.data.content,
             console.log(this.watchTableCell)
              this.dataObj4 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId4 = !this.fileId4;
      
          }
        })
        .catch(function(error) {});
      // -----------------
      let data2 = {
        relationBusId: val2
      };
      data2 = JSON.stringify(data2);
      this.axios
        .post(`/process/infProcessAppend/getInfProcessAppendRows`, data2, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.appendId = response.data.id;
            this.watchTableCell.val11 = response.data.content;
            this.dataObj5 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId5 = !this.fileId5;
          }
        })
        .catch(function(error) {});
      // ---------------
      this.watchTableCellDitalModel = true;
    },
  }
};
</script>


